import Grid from "@mui/material/Grid";
import Paper from "@mui/material/Paper";
import Stack from "@mui/material/Stack";
import Container from "@mui/material/Container";
import React from "react";
import FormatListBulletedIcon from "@mui/icons-material/FormatListBulleted";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import makeStyles from "@mui/styles/makeStyles";
import {
  Banner,
  SearchBar,
  CardImg,
  ProfileBar,
  BottomNav,
} from "../../components";
import { useNavigate } from "react-router-dom";
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, { Pagination } from "swiper";
import pelayananImg from "../../assets/pelayanan.png";
import HemodialisaImg from "../../assets/hemodialisa.png";
import DokterImg from "../../assets/dokter.png";
import BedImg from "../../assets/info_kamar.png";
import MCUImg from "../../assets/mcu.png";
import quickTestImg from "../../assets/quick_test.png";
import kehamilanImg from "../../assets/kehamilan.png";
import vaksinImg from "../../assets/vaksin.png";
import DialogBahasa from "../../components/DialogBahasa";
import { connect } from "react-redux";
import TranslateIcon from "@mui/icons-material/Translate";
import { NTRL_100 } from "../../theme/colors";
import { resetAppointment, setBahasaOpen } from "../../redux/actions";

const useStyles = makeStyles(() => ({
  textContent: {
    padding: "0 8px !important",
    flexGrow: 100,
  },
  filterContent: {
    flexGrow: 100,
  },
  searchBar: {
    display: "flex",
  },
  root: {
    paddingTop: "16px",
  },
  iconBahasa: {
    position: "absolute",
    bottom: "15vh",
    right: "8px",
    zIndex: "1000",
    backgroundColor: NTRL_100,
    borderRadius: "50%",
  },
}));

const HomePage = (props) => {
  const classes = useStyles();
  const { stateBahasa, actSetBahasaOpen, stateUser, actResetAppointment } =
    props;
  const navigate = useNavigate();

  const contentSlide = [
    {
      title: stateBahasa === "indonesia" ? "Daftar Online" : "Registration",
      click: () => {
        actResetAppointment();
        navigate(stateUser ? "/make-appointment" : "/register");
      },
      // disabled: true,
      img: pelayananImg,
    },
    {
      title: stateBahasa === "indonesia" ? "Dokter" : "Doctor",
      click: () => navigate("/doctor-home"),
      disabled: false,
      img: DokterImg,
    },
    {
      title: stateBahasa === "indonesia" ? "Info Kamar" : "Room Info",
      click: () => navigate("/rooms"),
      disabled: false,
      img: BedImg,
    },
    {
      title: "MCU",
      click: () => navigate("/mcu"),
      disabled: false,
      img: MCUImg,
    },
    {
      title: stateBahasa === "indonesia" ? "Test Cepat" : "Quick Test",
      click: () => navigate("/make-appointment"),
      disabled: true,
      img: quickTestImg,
    },
    {
      title: stateBahasa === "indonesia" ? "Vaksin" : "Vaccinate",
      click: () => navigate("/register-vaksin"),
      // disabled: true,
      img: vaksinImg,
    },
    {
      title: stateBahasa === "indonesia" ? "Kehamilan" : "Pregnancy",
      click: () => navigate("/make-appointment"),
      disabled: true,
      img: kehamilanImg,
    },
    {
      title: stateBahasa === "indonesia" ? "Hemodialisa" : "Hemodialysis",
      click: () => navigate("/make-appointment"),
      disabled: true,
      img: HemodialisaImg,
    },
  ];
  return (
    <Container className={classes.root}>
      <DialogBahasa />
      <div className={classes.iconBahasa}>
        <IconButton onClick={() => actSetBahasaOpen(true)}>
          <TranslateIcon fontSize="large" />
        </IconButton>
      </div>
      <Stack spacing={2}>
        <ProfileBar />
        <Typography variant="subtitle2">
          {stateBahasa === "indonesia" ? "Promosi" : "Promotion"}
        </Typography>
        <Swiper
          // pagination={true}
          slidesPerView={2.1}
          spaceBetween={10}
          className="mySwiper"
        >
          {[1, 2, 3, 4].map((item, index) => {
            return (
              <SwiperSlide
                key={"carousel_" + index}
                onClick={() => navigate("/news-page")}
              >
                <Banner />
              </SwiperSlide>
            );
          })}
        </Swiper>
        <Grid container>
          <Grid container item xs={12} spacing={2}>
            {contentSlide.map((item, index) => {
              return (
                <Grid item xs={3} key={"category" + index}>
                  <CardImg
                    type="small"
                    title={item.title}
                    text="Bedah Hati"
                    click={item.click}
                    disabled={item.disabled}
                    img={item.img}
                  />
                </Grid>
              );
            })}
          </Grid>
        </Grid>
        {/* <Typography variant="subtitle2">
          {stateBahasa === "indonesia" ? "Berita" : "News"}
        </Typography>
        <Swiper
          // pagination={true}
          slidesPerView={2.1}
          spaceBetween={10}
          className="mySwiper"
        >
          {[1, 2, 3, 4].map((item, index) => {
            return (
              <SwiperSlide
                key={"carousel_" + index}
                onClick={() => navigate("/news-page")}
              >
                <Banner />
              </SwiperSlide>
            );
          })}
        </Swiper> */}
      </Stack>
      <BottomNav />
    </Container>
  );
};

const mapStateToProps = (state) => {
  return {
    stateBahasa: state.app.bahasa,
    stateUser: state.auth.user,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    actSetBahasaOpen: (body) => dispatch(setBahasaOpen(body)),
    actResetAppointment: (body) => dispatch(resetAppointment(body)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(HomePage);
